import React, { useState,useRef,useEffect } from 'react';
import gsap, { Sine,TweenMax,SteppedEase,Power4 } from 'gsap';


const Login = ({

}) =>{
        useEffect(
		() => { 
                        gsap.to("#turbwave", 8, {
                                attr:{"baseFrequency":0.01},
                                repeat:-1,
                                yoyo:true
                              });
	},[ ]
    );

	return (
        <>
{/* https://klevron.github.io/codepen/misc/water-texture.jpg */}
                {/* <svg width="250" height="250">
                        <filter id='noise' x='0%' y='0%' width='100%' height='100%'>
                        <feTurbulence baseFrequency="0.03" />
                        </filter>
                        <rect x="0" y="0" width="100%" height="100%" filter="url(#noise)" fill="none"/>
                </svg> */}
                <svg class="mysvg" width="538" height="190" viewBox="0 0 600 200">
                <defs> 
                        <filter id="turb">
                        <feTurbulence id="turbwave" type="fractalNoise" baseFrequency="0.03" numOctaves="2"
                                result="turbulence_3" data-filterId="3" />
                        <feDisplacementMap xChannelSelector="R" yChannelSelector="G" in="SourceGraphic" in2="turbulence_3" scale="40" />
                        </filter>
                </defs>
                <image id="img" x="0" y="0" width="100%" height="100%" xlinkHref="https://s3-us-west-2.amazonaws.com/s.cdpn.io/56901/greensock.png" filter="url(#turb)" />
                </svg>
        </>

	);
}

export default Login;